body {
  color: #333;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  width: 85%;
  max-width: 1100px;
  margin: auto;
}

header {
  color: #fff;
  text-align: center;
  background: linear-gradient(to right, #333, #444);
  padding: 20px 0;
  box-shadow: 0 4px 6px #0000001a;
}

header h1 {
  margin-bottom: 10px;
  font-size: 28px;
}

nav {
  justify-content: center;
  gap: 20px;
  display: flex;
}

nav a {
  color: #fff;
  padding: 8px 15px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

nav a:hover {
  background-color: #0073e6;
  border-radius: 4px;
}

section {
  background: #fff;
  border-radius: 8px;
  margin: 20px auto;
  padding: 50px 0;
  box-shadow: 0 4px 8px #0000001a;
}

h2 {
  text-align: center;
  color: #222;
  margin-bottom: 15px;
  font-size: 28px;
}

.project-card {
  background: #eaeaea;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  transition: transform .3s ease-in-out;
}

.project-card:hover {
  transform: scale(1.03);
}

.project-card a {
  color: #0073e6;
  font-weight: bold;
  text-decoration: none;
}

.project-card a:hover {
  text-decoration: underline;
}

#contact p {
  text-align: center;
  font-size: 18px;
}

#contact a {
  color: #0073e6;
  font-weight: bold;
  text-decoration: none;
}

#contact a:hover {
  text-decoration: underline;
}

footer {
  color: #fff;
  text-align: center;
  background: #222;
  margin-top: 20px;
  padding: 15px;
}
